import React, { useState } from 'react'
//import "../TabMenu/tab.css";
import Menu from "./menu";
import "../tabMenu/gallery.css"
import MenuItems from './MenuItems';
import CatMenu from './CatMenu';

const catValues=[...new Set(Menu.map((curElem)=>{
    return curElem.category;

})),"all"]
console.log(catValues);
const Gallery = () => {

    const [item, setItems] = useState(Menu);
    const [catItem, setCatItem] = useState(catValues);
    const filterItem = (categItem) => {

        if(categItem==="all"){
            setItems(Menu);
            return;
        }
        const updatedItems = Menu.filter((curElem) => {
            return curElem.category === categItem;
        });


        setItems(updatedItems);
    }

    return (
        <>
            <div className="heading">
                <h1 className="mt-5 text-center main-heading">Order Your Favourite Dish</h1>
            </div>
            <hr />
           <CatMenu filterItem={filterItem} catItem={catItem}/>
            {/* my main items section  */}
          <MenuItems item={item}/>

        </>
    )
}

export default Gallery